.nav {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  list-style: none;
}

.item {
  padding: 15px 15px;
  text-align: center;

  width: 100px;
  margin-right: 20px;
}

.text {
  text-decoration: none;
  width: 100%;
  padding: 20px;
  color: #09aa21;
  font-weight: 500;

  transition: all 300ms ease-in-out;
}
.btn {
  width: 100%;
  padding: 20px;

  border-radius: 20px;
  border: 1px solid #09aa21;

  color: #09aa21;
  background-color: transparent;

  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: 350ms cubic-bezier(0.455, 0.03, 0.515, 0.955);
}

.btn:hover {
  color: #fff;
  background-color: #09aa21;

  box-shadow: 2px 0px 31px 3px rgba(9, 170, 33, 0.51);
  -webkit-box-shadow: 2px 0px 31px 3px rgba(9, 170, 33, 0.51);
  -moz-box-shadow: 2px 0px 31px 3px rgba(9, 170, 33, 0.51);
}
